import { useState } from 'react';
import {
  SecondSectionBackground,
  SecondSectionWrapper,
  SecondSectionSubtitle,
  Content,
  ImageWrapper,
  Image,
  DoctorName,
  DoctorTitle,
  DoctorPosition,
  DoctorTextTitle,
  DoctorText,
  Button,
  Bold,
  TextContent,
  DoctorContent,
} from './SecondSection.styled';
import doctor from '../../../../assets/Desktop/SlimStick-strona2-doktor.png';

const SecondSection = () => {
  const [showMore, setShowMore] = useState(false);

  return (
    <SecondSectionBackground>
      <SecondSectionWrapper>
        <SecondSectionSubtitle>
          SlimStick Patches polecane przez cenionych specjalistw w dziedzinie
          odchudzania
        </SecondSectionSubtitle>
        <Content centerColumnBoth>
          <DoctorContent>
            <ImageWrapper>
              <Image src={doctor} alt='Mariusz Rogala portrait photography' />
            </ImageWrapper>
            <DoctorName>
              <DoctorTitle>dr hab.</DoctorTitle> <Bold>Mariusz Rogala</Bold>
            </DoctorName>
            <DoctorPosition>
              dietetyk z 15-letnim dowiadczeniem, naukowiec
            </DoctorPosition>
          </DoctorContent>
          <TextContent>
            <div>
              <DoctorTextTitle>Dzie dobry,</DoctorTextTitle>
              <DoctorText>
                nazywam si Mariusz Rogala i od ponad 15 lat prowadz prywatny
                gabinet w Lublinie, w ktrym codziennie przyjmuj dziesitki
                osb zmagajcych si z nadprogramowymi kilogramami, zym
                cholesterolem i brakiem energii. Do kadej ze zgaszajcych si
                do mnie osb, podchodz w indywidualny sposb, dopasowujc diet
                do wieku, choroby i innych wanych czynnikw majcych wpyw na
                wag.
              </DoctorText>
            </div>
            <Button onClick={() => setShowMore(!showMore)}>Czytaj dalej</Button>
            {showMore && (
              <div>
                <DoctorText>
                  Teraz jest to ju jednak nieistotne, w jakim jeste wieku,
                  gdzie mieszkasz i ile waysz. Jeeli to wanie czytasz, to
                  znaczy, e od wymarzonej figury dzieli Ci zaledwie jeden
                  krok!
                </DoctorText>
                <DoctorText>
                  Pewnego dnia w moim gabinecie pojawi si pacjent, na ktrego
                  nie dziaay adne diety. Znajoma polecia mu plastry
                  transdermalne, ktre postanowi mi pokaza i zapyta, czy aby
                  na pewno nie pogorsz jego i tak kiepskiej sytuacji.
                </DoctorText>
                <DoctorText>
                  Zaintrygowany szukaem informacji dotyczcych tej rewolucyjnej
                  metody. W kocu trafiem na sprawozdanie z konferencji
                  naukowej "Odchudzanie na miar XXI wieku", ktra odbya si w
                  renomowanym orodku ds. prawidowego ywienia w Genewie.
                  Opisywano tam dziaanie tej transdermalnej formuy. Okazuje
                  si, e w peni naturalne skadniki zawarte w plastrach
                  odchudzajcych wpywaj na gospodark wodn organizmu oraz
                  rozbijaj zgromadzon na brzuchu, udach, poladkach czy innych
                  czciach ciaa tkank tuszczow. Skadniki aktywne zawarte w
                  plastrach dziaaj przez 24/h na dob i przenikaj przez skr
                  waciw, co pozwala na nawet 7 - krotne przyspieszenie
                  metabolizmu, zwikszenie energii i wigoru, a w konsekwencji na
                  trwae pozbycie si zbdnych kilogramw. Od tego czasu polecam
                  te plastry wszystkim moim pacjentom i z dnia na dzie
                  przekonuje si do tego, e plastry transdermalne SlimStick
                  Patches to najlepsze rozwizanie dla osb chccych odzyska
                  modziecz sylwetk.
                </DoctorText>
                <DoctorText>
                  Z powaaniem <br /> <Bold>dr hab. Mariusz Rogala</Bold>
                </DoctorText>
              </div>
            )}
          </TextContent>
        </Content>
      </SecondSectionWrapper>
    </SecondSectionBackground>
  );
};

export default SecondSection;
