import { useContext } from 'react';
import { useHistory } from 'react-router-dom';
import { OfferInfoItem } from './PageComponents/blocks/index';
import {FormContext} from '../../contexts/FormContext';
import { infoData } from './Data/infoData';

import {
  PageWrapper,
  PageHeader,
  SubHeader,
  ParagraphOffer,
  ContentImageWrapper,
  ContentWrapper,
  ImageWrapper,
  LinkPrimary,
  LinkSecondary,
} from './SubPageThreeRegular.styles.js';

import img_stick from './Assets/regular.png';

const offerInfo = infoData.offerInfo;

const offer = offerInfo.map((item, index) => {
  return <OfferInfoItem key={index + 'jfYTfCVc12hv'} content={item} />;
});

const SubPageThreeRegular = () => {
  const {
    setSelectedProduct,
    productRegularPackageWithCareDetails
  } = useContext(FormContext);

  const history = useHistory();

  const addCare = () => {
    setSelectedProduct(productRegularPackageWithCareDetails)
    history.push('/subpagefinal')
    window.scrollTo(0, 0);
  };
  
  const leaveWithregular = () => {
    history.push('/subpagefinal')
    window.scrollTo(0, 0);
  }

  return (
    <PageWrapper>
      <PageHeader>
        Uwaga: wane informacje o zamwieniu <br /> [dofinansowanie!]
      </PageHeader>
      {offer}
      <SubHeader as={PageHeader}>Dlaczego warto skorzysta z oferty?</SubHeader>
      <ParagraphOffer>
        1. Pamitaj, e zastosowanie rozszerzonej kuracji gwarantuje Ci
        osignicie efektw przedstawionych na tej stronie jeszcze skuteczniej i
        to w znacznie krtszym czasie!
      </ParagraphOffer>
      <ContentImageWrapper>
        <ContentWrapper>
          <ParagraphOffer>
            2. Odbir rozszerzonego pakietu to najwygodniejsza i najbardziej
            opacalna opcja, ktra pozwala na jak najszybsze osignicie
            podanych rezultatw (przerwa spowodowana oczekiwaniem na kolejn
            wysyk, ktra bdzie kontynuacj zabiegu, moe spowolni efekty).
          </ParagraphOffer>
          <ParagraphOffer>
            3. Europejski Certyfikat Najwyszej Jakoci obejmuje tylko
            rozszerzon kuracj. Decydujc si na to rozwizanie, otrzymujesz
            take profesjonaln opiek specjalisty. Dziki temu, jeli z
            jakiego powodu efekty nie speni Twoich oczekiwa, otrzymasz
            dodatkowe wsparcie, w peni dostosowane do indywidualnych potrzeb
            Twojego organizmu.
          </ParagraphOffer>
        </ContentWrapper>
        <ImageWrapper>
          <img src={img_stick} alt='slimStick'></img>
        </ImageWrapper>
      </ContentImageWrapper>
      <ParagraphOffer>
        Oferta jest ograniczona czasowo i ilociowo. W zwizku z tym,
        rozszerzony pakiet kuracji dostpny jest tylko dla osb, ktre do tej
        pory zdecydoway si na mae opakowanie (zostao tylko 6 pakietw
        promocyjnych!). Bdzie aktywny tylko przez 5 minut, wic dziaaj szybko!
      </ParagraphOffer>
      <LinkPrimary onClick={addCare}>
        Tak, chc skorzysta z oferty specjalnej oraz otrzyma 3 miesice
        profesjonalnej opieki specjalisty.
      </LinkPrimary>
      <LinkSecondary onClick={leaveWithregular}>
        Nie, nie potrzebuj ju gwarancji, e osign swj cel.
      </LinkSecondary>
    </PageWrapper>
  );
};

export default SubPageThreeRegular;
