import { useContext } from 'react';
import { OfferInfoItem, FormComponent } from './PageComponents/blocks/index';
import { FormContext } from '../../contexts/FormContext';
import { infoData } from './Data/infoData';

import {
  PageWrapper,
  PageHeader,
  SubHeader,
  ParagraphOffer,
  ContentImageWrapper,
  ImageWrapper,
  TitleInfo,
  PricesWrapper,
  PriceItem,
  PriceItemLight,
  PriceItemDark,
  PriceContent,
  PriceContentMain,
  InfoContent,
  Bold,
} from './SubPageForm.styles.js';

import img_stick from './Assets/regular.png';

const offerInfo = infoData.offerInfo;

const offer = offerInfo.map((item, index) => {
  return <OfferInfoItem key={index + 'jfYTfCVc12hv'} content={item} />;
});

const SubPageThreeRegular = () => {
  const { selectedProduct } = useContext(FormContext);

  return (
    <PageWrapper>
      <PageHeader>Dziki za cierpliwo, to ju ostatni krok!</PageHeader>
      <TitleInfo as={ParagraphOffer}>
        Twoje zamwienie zostao wanie zarejestrowane w systemie. W cigu
        kilku godzin skontaktuje si z Tob wykfalifikowany pracownik Biura
        Obsugi Klienta w celu realizacji zamwienia.
      </TitleInfo>
      <SubHeader as={PageHeader}>Podsumowanie zamwienia</SubHeader>
      <ContentImageWrapper>
        <ImageWrapper>
          <img src={img_stick} alt='small bottle'></img>
        </ImageWrapper>
        <PricesWrapper>
          <PriceItem>
            <PriceContentMain as={PriceContent}>
              <span>
                <span>Slim</span>Stick Patches
              </span>{' '}
              w promocyjnej cenie:
            </PriceContentMain>
            <PriceContent>
              <span>{selectedProduct.price} z</span>
            </PriceContent>
          </PriceItem>
          <PriceItemLight as={PriceItem}>
            <PriceContent>Dostawa</PriceContent>
            <PriceContent>
              <span>{selectedProduct.delivery} z</span>
            </PriceContent>
          </PriceItemLight>
          <PriceItemDark as={PriceItem}>
            <PriceContent>Podsumowanie</PriceContent>
            <PriceContent>
              {selectedProduct.price + selectedProduct.delivery} z
            </PriceContent>
          </PriceItemDark>
        </PricesWrapper>
      </ContentImageWrapper>
      <InfoContent>
        <Bold>Wana informacja:</Bold> w cigu najbliszych 15 sekund{' '}
        <Bold underline>bez dodatkowych kosztw</Bold>{' '}
        moesz wasnorcznie doda adres na ktry ma by wysana paczka i nada
        jej priorytet. Dziki temu Twoje zamwienie zostanie wysane jako
        pierwsze i bdziesz mg przekona si o efektach uytkowania produktu
        jeszcze szybciej!
      </InfoContent>
      <SubHeader as={PageHeader}>Jak to zrobi?</SubHeader>
      {offer}
      <FormComponent />
    </PageWrapper>
  );
};

export default SubPageThreeRegular;
