import React from 'react';

import {
  MobileOfferWrapper,
  TableOfferWrapper,
  HeaderTableOffer,
  TableImage,
  TableContent
} from './MobileOfferTable.styles.js'


const MobileOfferTable = ({ ...props }) => {
  return(
    <MobileOfferWrapper>
      <TableOfferWrapper>
        <HeaderTableOffer>
          <strong>2 mae opakowania</strong> <br />
          (oferta regularna)
        </HeaderTableOffer>
        <TableImage>
          <img src={ props.img1 } alt='sensual shape'></img>
        </TableImage>
        <TableContent>
          <strong>Ilo plastrw</strong> <br />
          2 x 15 = 30
        </TableContent>
        <TableContent>
          <strong>Wysyka</strong> <br />
          2  30 = 60 z
        </TableContent>
        <TableContent>
          <strong>Cena</strong> <br />
          634 z + 60 z = 694 z
        </TableContent>
      </TableOfferWrapper>

      <TableOfferWrapper dark>
        <HeaderTableOffer dark>
          <strong>1 DUE opakowanie</strong> <br />
          (oferta specjalna)
        </HeaderTableOffer>
        <TableImage>
          <img src={ props.img2 } alt='sensual shape'></img>
        </TableImage>

        <TableContent>
          <strong>Ilo plastrw</strong> <br />
          60
        </TableContent>
        <TableContent>
          <strong>Wysyka</strong> <br />
          0 z
        </TableContent>
        <TableContent>
          <strong>Cena</strong> <br />
          234 z
        </TableContent>
      </TableOfferWrapper>
      
    </MobileOfferWrapper>
  )
}

export default MobileOfferTable;