import { useContext } from 'react';
import { useHistory } from 'react-router-dom';
import { OfferInfoItem } from './PageComponents/blocks/index';
import { infoData } from './Data/infoData';
import { FormContext } from '../../contexts/FormContext';

import {
  PageWrapper,
  PageHeader,
  SubHeader,
  ParagraphOffer,
  ParagraphOfferBottom,
  ContentImageWrapper,
  ContentWrapper,
  ImageWrapper,
  LinkPrimary,
  LinkSecondary,
} from './SubPageThreeSet.styles.js';

import img_bottle from './Assets/zestaw.png';

const offerInfo = infoData.offerInfo;

const offer = offerInfo.map((item, index) => {
  return <OfferInfoItem key={index + 'jfYTfCVc12hv'} content={item} />;
});

const SubPageThreeRegular = () => {
  const { setSelectedProduct, productSetPackageWithCareDetails } = useContext(
    FormContext
  );

  const history = useHistory();

  const addCare = () => {
    setSelectedProduct(productSetPackageWithCareDetails);
    history.push('/subpagefinal');
    window.scrollTo(0, 0);
  };

  const leaveWithBig = () => {
    history.push('/subpagefinal');
    window.scrollTo(0, 0);
  };

  return (
    <PageWrapper>
      <PageHeader>
        Uwaga: wane informacje dotyczce Twojego <br /> zamwienia
        [dofinansowanie!]
      </PageHeader>
      {offer}
      <SubHeader as={PageHeader}>Dlaczego to jest takie opacalne?</SubHeader>
      <ParagraphOffer>
        Dziki dofinansowaniu i tylko niewielkiej dopacie (dofinansowanie 88%
        ceny podstawowej!) wraz z zamwieniem otrzymasz dodatkowe opakowanie
        SlimStick Patches.
      </ParagraphOffer>
      <ContentImageWrapper>
        <ContentWrapper>
          <ParagraphOffer>
            Dziki temu zyskujesz gwarancj efektw, nawet jeli naleysz do
            grona osb najbardziej odpornych na wpyw tej biomedycznej formuy.
          </ParagraphOffer>
          <ParagraphOffer>
            <strong>Dlatego jeli:</strong>
          </ParagraphOffer>
          <ParagraphOffer>
            1. Chcesz wreszcie poczu ulg, uzyska idealn wag i czu si
            dobrze w swoim ciele,
          </ParagraphOffer>
          <ParagraphOffer>
            2. Wielokrotnie testowae rne metody, ale nie osigne na stae
            oczekiwanych rezultatw,
          </ParagraphOffer>
          <ParagraphOffer>
            3. Ze wzgldu na stres i zmiany yciowe, nie moesz poczu peni
            efektw kuracji
          </ParagraphOffer>
        </ContentWrapper>
        <ImageWrapper>
          <img src={img_bottle} alt='small bottle'></img>
        </ImageWrapper>
      </ContentImageWrapper>
      <ParagraphOffer>
        ...skorzystaj z tej dodatkowej promocji, ktra pozwoli Ci na zawsze
        skutecznie wyeliminowa irytujcy problem.
      </ParagraphOffer>
      <SubHeader as={PageHeader}>Ale to nie wszystko!</SubHeader>
      <ParagraphOfferBottom as={ParagraphOffer}>
        Dziki dofinansowaniu otrzymujesz rwnie{' '}
        <strong>profesjonalne wsparcie specjalisty na okres 3 miesicy</strong>.
      </ParagraphOfferBottom>
      <ParagraphOfferBottom as={ParagraphOffer}>
        Dziki temu, <span>jeli efekty kuracji Ci nie zadowol</span>,{' '}
        <strong>otrzymasz profesjonaln pomoc!</strong> Naszym zadaniem bdzie
        dostosowanie lub zmodyfikowanie leczenia tak, aby jak najszybciej
        osign Twoje cele. Bez stresu i przemczenia!.
      </ParagraphOfferBottom>
      <ParagraphOfferBottom as={ParagraphOffer}>
        Niestety liczba osb, ktre otrzymaj dofinansowanie jest ograniczona i
        (ze wzgldu na due zainteresowanie) bardzo szybko maleje. Jeli wic
        chcesz otrzyma dodatkowe opakowanie i wsparcie specjalisty,{' '}
        <strong>dziaaj natychmiast..</strong>
      </ParagraphOfferBottom>
      <LinkPrimary onClick={addCare}>
        Tak, chc skorzysta ze specjalnego dofinansowania i otrzyma 2 due
        opakowania oraz gratis 3 miesice profesjonalnej opieki specjalisty
      </LinkPrimary>
      <LinkSecondary onClick={leaveWithBig}>
        Nie, nie potrzebuj ju gwarancji, e osign swj cel.
      </LinkSecondary>
    </PageWrapper>
  );
};

export default SubPageThreeRegular;
