import {
  EightSectionBackground,
  EightSectionWrapper,
  EightSectionSubtitle,
  Content,
  ImageWrapper,
  ImageWrapperCertificate,
  Image,
  Text,
  TextWrapper,
} from './EightSection.styled';
import { OrangeText } from '../../blocks/index';
import certificateTitle from '../../../../assets/Desktop/certificate-title.png';
import certificate from '../../../../assets/Desktop/certificate.png';

const EightSection = () => {
  return (
    <EightSectionBackground>
      <EightSectionWrapper>
        <EightSectionSubtitle>
          Skorzystaj z rewolucyjnego sposobu utraty wagi{' '}
          <OrangeText>SlimStick Patches bez adnego ryzyka</OrangeText>
        </EightSectionSubtitle>
        <Content>
          <TextWrapper>
            <ImageWrapper>
              <Image src={certificateTitle} alt='Certificate title' />
            </ImageWrapper>
            <Text>
              Ten unikalny produkt zosta objty Europejsk Gwarancj Najwyszej
              Jakoci. To certyfikat przyznawany innowacyjnym rozwizaniom,
              ktre stale przyczyniaj si do rozwoju najnowszych technologii.
              Sekretarz i Prezes Zarzdu Europejskiej Kapituy Jakoci i
              Skutecznoci przyznaj go jedynie formuom i produktom poddanym
              serii rygorystycznych bada klinicznych. To midzynarodowe
              wyrnienie potwierdza wysok skuteczno i bezpieczestwo
              uytkowania kuracji.
            </Text>
          </TextWrapper>
          <ImageWrapperCertificate>
            <Image src={certificate} alt='certificate' />
          </ImageWrapperCertificate>
        </Content>
      </EightSectionWrapper>
    </EightSectionBackground>
  );
};

export default EightSection;
